#homepage-body {

}

#homepage-header-container-left {
    position: absolute;
    -webkit-transform: rotate(60deg);
    background: -webkit-linear-gradient(0deg, #cccccc, #64b1ff);
    width: 1500px;
    height: 1500px;
}

#homepage-header-container-right {
    z-index: 90;
    position: fixed;
    right: 0px;
    width: 200px;
    height: 100%;
    opacity: 0.5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.navigation-item-container {
    cursor: pointer;
    width: 80%;
    height: 20%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.navigation-item-text {
    font-size: 20pt;
}

.navigation-item-container:hover .navigation-item-text {
    font-size: 25pt;
}

#homepage-main-info-container {
    width: auto;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    position: absolute;
}

#homepage-main-info-title {
    padding-bottom: 10%;
}

#homepage-work-experience-container {
    position: absolute;
    width: 100%;
    height: auto;
}